<template>
    <div>
        <!-- 标题 -->
        <van-nav-bar title="商品列表" left-arrow  @click-left="$router.back()"></van-nav-bar>
      <!-- 搜索栏 -->
      <van-search :value="getQuerySearch || '搜索商品'"  show-action placeholder="请输入搜索关键词" >
        <template #action>
        <div >搜索</div>
      </template>
      </van-search>
      <!-- 分类栏 -->
      <div class="classify">
        <div class="classify_item">综合</div>
        <div class="classify_item">销量</div>
        <div class="classify_item">价格</div>
      </div>
      <!-- 内容 -->
      <div class="goods-list">
      <GoodItem v-for="item in productList" :key="item.goods_id" :item="item"></GoodItem>
      </div>
    </div>
</template>

<script>
import GoodItem from '@/components/GoodItem.vue'
import { getProduct } from '@/api/product'
export default {
  name: 'searchListIndex',
  data () {
    return {
      page: 1,
      productList: []
    }
  },
  async created () {
    const { data: { list } } = await getProduct({
      categoryId: this.$route.query.categoryId,
      goodsName: this.getQuerySearch,
      page: this.page
    })
    this.productList = list.data
  },
  components: {
    GoodItem
  },
  computed: {
    getQuerySearch () {
      return this.$route.query.search
    }
  }
}
</script>

<style scoped>
.classify{
  width: 100%;
  height: 50px;
  display: flex;
}
.classify_item{
  width: calc(100%/3);
  height: 100%;
  line-height: 50px;
  text-align: center;
}
</style>
